import axios from "axios";
import { getEnvVariableValue } from "utils/CommonUtil";
import HelperService from "./HelperService";

type HTTP_METHODS = "get" | "post" | "put" | "delete" | "patch";

const DEFAULT_API_PATH = getEnvVariableValue("VITE_BASE_API_URL");
const DEFAULT_API_PATH_TENANT = window.location.protocol + "//" + window.location.host + "";

class BaseApiService {
  constructor(props?: any) {
    console.log("BaseApiService");
  }
  getDefaultApiUrl() {
    return DEFAULT_API_PATH_TENANT;
  }

  getAxios() {
    return axios;
  }

  makeGetRequestWithAuth(url: string, headers?: any) {
    url = `${getEnvVariableValue("VITE_BASE_API_URL")}${url}`;
    const headersObj = {
      headers: { ...HelperService.getHeadersWithAuth(), ...(headers || {}) },
    };

    return this.getAxios().get(url, headersObj);
  }
  makeGetRequest(url: string, headers?: any) {
    url = `${getEnvVariableValue("VITE_BASE_API_URL")}${url}`;
    const headersObj = {
      headers: { ...(headers || {}) },
    };
    return this.getAxios().get(url, headersObj);
  }

  makePostRequestWithAuth(url: string, body?: any, headers?: any) {
    url = `${getEnvVariableValue("VITE_BASE_API_URL")}${url}`;
    const headersObj = {
      headers: { ...HelperService.getHeadersWithAuth(), ...(headers || {}) },
    };
    return this.getAxios().post(url, body, headersObj);
  }
  makePostRequest(url: string, body?: any, headers?: any) {
    url = `${getEnvVariableValue("VITE_BASE_API_URL")}${url}`;
    const headersObj = {
      headers: { ...(headers || {}) },
    };
    return this.getAxios().post(url, body, headersObj);
  }

  makePutRequestWithAuth(url: string, body?: any, headers?: any) {
    url = `${getEnvVariableValue("VITE_BASE_API_URL")}${url}`;
    const headersObj = {
      headers: { ...HelperService.getHeadersWithAuth(), ...(headers || {}) },
    };
    return this.getAxios().put(url, body, headersObj);
  }
  makePutRequest(url: string, body?: any, headers?: any) {
    url = `${getEnvVariableValue("VITE_BASE_API_URL")}${url}`;
    const headersObj = {
      headers: { ...(headers || {}) },
    };
    return this.getAxios().put(url, body, headersObj);
  }

  makeDeleteRequestWithAuth(url: string, headers?: any) {
    url = `${getEnvVariableValue("VITE_BASE_API_URL")}${url}`;
    const headersObj = {
      headers: { ...HelperService.getHeadersWithAuth(), ...(headers || {}) },
    };
    return this.getAxios().delete(url, headersObj);
  }
  makeDeleteRequest(url: string, headers?: any) {
    url = `${getEnvVariableValue("VITE_BASE_API_URL")}${url}`;
    const headersObj = {
      headers: { ...(headers || {}) },
    };
    return this.getAxios().delete(url, headersObj);
  }

  axiosConfig(
    url: string,
    method: HTTP_METHODS,
    headers: any = HelperService.getHeaders() ?? undefined,
    data?: any
  ) {
    const config = {
      url: `${getEnvVariableValue("VITE_BASE_API_URL")}${url}`,
      method,
      headers: {
        ...headers,
      },
      data: data,
    };
    //@ts-ignore: will be fixed in future
    return axios(config);
  }
  axiosConfigWithAuth(url: string, method: HTTP_METHODS, headers?: any, data?: any) {
    let config = {};
    config = {
      url: `${getEnvVariableValue("VITE_BASE_API_URL")}${url}`,
      method,
      headers: { ...HelperService.getHeadersWithAuth(), ...(headers || {}) },
      data: data,
    };
    // console.log("config.header ", config);
    //@ts-ignore: Unreachable code error
    return axios(config);
  }
}

export default BaseApiService;
