export function checkDataHasValue(obj: any) {
  for (const key in obj) {
    const value = obj[key];
    if (
      value !== "" &&
      value !== undefined &&
      value !== null &&
      value !== false &&
      (Array.isArray(value) ? value.length !== 0 : true) &&
      (typeof value !== "object" || Object.keys(value).length !== 0)
    ) {
      return true;
    }
  }

  return false;
}

